<template>
    <div id="publish-article">
        <el-row>
            <el-col>
                <el-form :model="form" :rules="formRules" ref="publish_article_form">
                    <el-form-item label="所属项目" label-width="100px" prop="project_uuid">
                        <el-select v-model="form.project_uuid" placeholder="请选择">
                            <el-option
                                v-for="item in projects"
                                :key="item.uuid"
                                :label="item.name"
                                :value="item.uuid">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="标题" label-width="100px" prop="title">
                        <el-input v-model="form.title"></el-input>
                    </el-form-item>
                    <el-form-item label="子标题" label-width="100px" prop="sub_title">
                        <el-input type="textares" v-model="form.sub_title"></el-input>
                    </el-form-item>
                    <el-form-item label="标签" label-width="100px" prop="sub_title">
                        <el-input placeholder="提示: 设置标签为了方便搜索" v-model="form.search_key"></el-input>
                    </el-form-item>
                    <el-form-item label="内容" label-width="100px" prop="describe">
                        <el-input type="textarea" rows="5" v-model="form.describe"></el-input>
                    </el-form-item>
                    <el-form-item class="text-center">
                        <el-button type="primary" class="dtt-width-30" @click="submit">提交</el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import {TABLE_PROJECT} from '../utils/bmob.tables';
    import {TABLE_ARTICLE} from '../utils/bmob.tables';
    import uuid from "uuid"

    export default {
        name: "publish-article",
        props:{
            mode:{type:String,default:"add"},
            article:{type:Object}
        },
        data() {
            return {
                submitBtnDisabled:true,
                form:{
                    project_uuid:"",
                    title:"",
                    sub_title:"",
                    describe:"",
                    search_key:""
                },
                formRules:{
                    project_uuid:{required:true,message:"请选择所属项目",trigger:'blur'},
                    title:{required:true,message:"请输入标题",trigger:'blur'},
                    sub_title:{required:true,message:"请输入子标题",trigger:'blur'},
                    describe:{required:true,message:"请输入项目描述",trigger:'blur'},
                    search_key:{required:true,message:"请输入搜索标签",trigger:'blur'},
                },
                projects:[]
            }
        },
        created() {
            this.getProjects();
        },
        methods:{
            initFormByProp(){
                this.form.title = this.article.title;
                this.form.sub_title = this.article.sub_title;
                this.form.describe = this.article.describe;
                this.form.project_uuid = this.article.project_uuid;
                this.form.search_key = this.article.search_key;
            },
            getProjects(){//获取项目列表
                const query = this.Bmob.Query(TABLE_PROJECT);
                let username = this.Bmob.User.current().username;
                query.equalTo("user_name","==",username);
                query.find().then(res => {
                    // console.log(res);
                    this.projects = res;
                    if(this.mode === "add"){
                        if(this.projects.length>0){
                            this.form.project_uuid = this.projects[0].uuid;
                        }
                    }
                    else {
                        // console.log(this.article);
                        this.initFormByProp();
                    }

                });
            },
            submit(){
                this.$refs["publish_article_form"].validate(valid => {
                    if(valid){
                        let objectId = this.Bmob.User.current().objectId;
                        let username = this.Bmob.User.current().username;
                        let acl = {};
                        acl[objectId] = {write:true};
                        acl["*"] = {read:true};
                        console.log(JSON.stringify(acl));
                        const query = this.Bmob.Query(TABLE_ARTICLE);
                        query.set("project_uuid",this.form.project_uuid);
                        query.set("uuid",uuid());
                        query.set("title",this.form.title);
                        query.set("sub_title",this.form.sub_title);
                        query.set("describe",this.form.describe);
                        query.set("search_key",this.form.search_key);
                        query.set("time",new Date().getTime());
                        query.set("user_name",username);
                        query.set("ACL",acl);
                        if(this.mode === "edit"){
                            query.set("id",this.article.objectId);
                        }
                        query.save().then(res => {
                            console.log(res);
                            this.$message.success("发布成功");
                            this.$emit("hide-dialog",false);
                        }).catch(err => {
                            console.log(err);
                            this.$message.error(err.error);
                        })
                    }
                    else {
                        this.$message("请检查必填项");
                    }
                })
            }
        }
    }
</script>
