<template>
    <div class="home">
        <el-container>
            <el-header class="navbar-fixed-top dtt-bg-color-white">
                <el-row>
                    <el-col>
                        <ul class="home-menu-ul">
                            <li class="home-menu-li home-menu-li-title">{{currentUser}}</li>
                            <li class="home-menu-li dtt-cursor-pointer home-menu-li-logout" @click="showLogoutVisible">退出</li>
                            <li class="home-menu-li dtt-cursor-pointer">
                                <router-link to="/back-manager">后台管理</router-link>
                            </li>
                        </ul>
                    </el-col>
                </el-row>
            </el-header>
            <el-main class="dtt-width-70 dtt-center dtt-margin-top-60">
                <el-row>
                    <el-col>
                        <el-form label-width="80px" label-position="left" :inline="true">
                            <el-form-item label="项目选择">
                                <el-select v-model="currentProject">
                                    <el-option value="-1" label="全部"></el-option>
                                    <el-option v-for="(obj,i) in projects" :key="i" :label="obj.name"
                                               :value="obj.uuid"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="内容查询">
                                <el-input v-model="searchText"></el-input>
                            </el-form-item>
                            <el-form-item label="时间查询">
                                <el-date-picker
                                    v-model="dateRange"
                                    type="daterange"
                                    range-separator="至"
                                    start-placeholder="开始日期"
                                    end-placeholder="结束日期">
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="getAllArticles">查询</el-button>
                                <el-button type="primary" @click="publishArticle">发布文章</el-button>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col v-for="(item,i) in datas" :key="i">
                        <div class="dtt-cursor-pointer">
                            <h3>{{item.title}}</h3>
                            <h4>{{item.sub_title}}</h4>
                            <article><pre>{{item.describe}}</pre></article>
                            <h5 class="text-right">{{item.updatedAt}}</h5>
                        </div>
                    </el-col>
                </el-row>
                <el-row class="text-right">
                    <el-col>
                        <el-pagination background layout="prev, pager, next"
                                       @current-change="pageChange"
                                       :total="totleCount" :page-size="5"/>
                    </el-col>
                </el-row>

                <el-dialog title="提示" :visible.sync="dialogLogoutVisible" width="30%">
                    <span>确定退出吗</span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogLogoutVisible = false">取 消</el-button>
                        <el-button type="primary" @click="logout">确 定</el-button>
                    </span>
                </el-dialog>

                <!--发布文章-->
                <el-dialog title="提示" :visible.sync="dialogPublishArticleVisible">
                    <publish-article v-if="dialogPublishArticleVisible" :mode="'add'" @hide-dialog="finishedPublishARticle"/>
                </el-dialog>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    import {TABLE_ARTICLE, TABLE_PROJECT} from "../utils/bmob.tables"
    import PublishArticle from "../components/publish.article";

    export default {
        name: 'home',
        components: {PublishArticle},
        data() {
            return {
                menus: [],
                datas: [],
                projects: [],
                currentProject: "-1",
                searchText: "",
                dateRange: "",
                pageNo: 1,
                totleCount: 0,
                pageSize: 5,
                dialogLogoutVisible:false,
                dialogPublishArticleVisible:false,
                currentUser:""
            }
        },
        methods: {
            finishedPublishARticle(){
                this.dialogPublishArticleVisible=false;
                this.getAllArticles();
            },
            publishArticle(){
                this.dialogPublishArticleVisible = true;
            },
            showLogoutVisible(){
                this.dialogLogoutVisible = true;
            },
            logout() {
                this.Bmob.User.logout();
                this.$router.replace("/login");
            },
            getAllArticles() {
                // console.log(this.currentProject);
                // console.log(this.dateRange);
                this.tableArticleTotleCount();

                const query = this.Bmob.Query(TABLE_ARTICLE);
                let queryArray = [];

                if (this.dateRange && this.dateRange.length > 0) {
                    let startDate = this.dateRange[0];
                    let endDate = this.dateRange[1];
                    if (startDate && endDate) {
                        const query1 = query.equalTo("time", ">", startDate.getTime());
                        const query2 = query.equalTo("time", "<", endDate.getTime());
                        queryArray.push(query1);
                        queryArray.push(query2);
                    }
                }

                if(this.searchText.length>0) {
                    const query1 = query.equalTo("search_key","==", this.searchText);
                    queryArray.push(query1);
                }

                if (this.currentProject !== "-1") {
                    console.log(this.currentProject);
                    const query2 = query.equalTo("project_uuid", "==", this.currentProject);
                    queryArray.push(query2);
                }

                console.log(queryArray.length);
                if (queryArray.length > 0) {
                    query.and(...queryArray);
                }
                query.limit(5);
                query.skip((this.pageNo - 1) * 5);
                query.find().then(res => {
                    console.log(res);
                    this.datas = res;
                });
            },
            getProjects() {
                const query = this.Bmob.Query(TABLE_PROJECT);
                // let userName = this.Bmob.User.current().username;
                // query.equalTo("user_name", "==", userName);
                query.find().then(res => {
                    console.log(res);
                    this.projects = res;
                });
            },
            tableArticleTotleCount() {
                const query = this.Bmob.Query(TABLE_ARTICLE);
                query.count().then(res => {
                    // console.log(`共有${res}条记录`);
                    this.totleCount = res;
                });
            },
            pageChange(page) {
                // console.log(page);
                this.pageNo = page;
                this.getAllArticles();
            }
        },
        created() {
            this.currentUser = this.Bmob.User.current().username+" 欢迎你";
            this.getAllArticles();
            this.getProjects();
        }
    }
</script>

